.button {
  display: block;
  font-size: 35px;
  line-height: 40px;
  padding: 5px 40px 15px;
  border-radius: 30px;
  background-image: linear-gradient(to bottom, #ffe402 0%, #ff0404 100%);
  /* display: flex; */
  /* align-items: center; */
  /* justify-content: center; */
}

.button:hover {
  background-image: linear-gradient(to bottom, #6a4eff 0%, #452ebf 100%);
}

.button__text {
  color: #fff;
}

@media screen and (max-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 24px;
    padding: 5px 30px 15px;
  }
}